import { Component, OnInit, OnDestroy } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { CoreService } from 'src/app/services/core/core.service';
import { LocalStorageService } from 'src/app/services/local-storage/local-storage.service';

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.css']
})
export class ChangePasswordComponent implements OnInit, OnDestroy {

  constructor(
    private fb: FormBuilder, private spinner: NgxSpinnerService, private coreService: CoreService,
    private localStorageService: LocalStorageService
  ) { }

  changePasswordForm = this.fb.group({
    oldPassword: ['', [Validators.required]],
    newPassword: ['', [Validators.required]],
    repeatPassword: ['', [Validators.required]]
  }, {
    validators: this.repeatPasswordValidator('newPassword', 'repeatPassword')
  });

  sucessMsg: string;
  errorMsg: string;

  ngOnInit(): void {
  }

  repeatPasswordValidator(controlName: string, matchingControlName: string) {
    console.log('check');

    return (formGroup: FormGroup) => {
      let control = formGroup.controls[controlName];
      let matchingControl = formGroup.controls[matchingControlName]
      if (matchingControl.errors && !matchingControl.errors.repeatPasswordValidator) {
        return;
      }
      if (control.value !== matchingControl.value) {
        matchingControl.setErrors({ repeatPasswordValidator: true });
      } else {
        matchingControl.setErrors(null);
      }
    };
  }

  clearMsg() {
    this.sucessMsg = '';
    this.errorMsg = '';
  }

  changePassword() {
    this.clearMsg();
    this.spinner.show();
    console.log('change form', this.changePasswordForm.value);

    let user = this.localStorageService.getUserData();
    let obj = {
      email: user.User.email,
      newPassword: this.changePasswordForm.get('newPassword').value,
      oldPassword: this.changePasswordForm.get('oldPassword').value
    }

    this.coreService.changePassword(obj).subscribe((changeRes: any) => {
      console.log('change pass res', changeRes);
      if (changeRes.status === true) {
        this.spinner.hide();

        this.sucessMsg = 'Password Changed Successfully.';
        this.changePasswordForm.reset();

      } else {
        this.spinner.hide();
        this.errorMsg = changeRes.message;
      }

    });
  }

  ngOnDestroy() {
    this.spinner.hide();
  }

}
